body {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    color: #B0BEC5;
    display: table;
    font-weight: 100;
    font-family: 'Lato';
}

.container {
    text-align: center;
    display: table-cell;
    vertical-align: middle;

    .content {
        text-align: center;
        display: inline-block;

        a {
            text-decoration: none;
        }

        .title {
            font-size: 72px;
            margin-bottom: 40px;
        }
    }
}
